import React, { useState, useEffect } from 'react'
import { useGlobalContext } from './context'
import { Link } from 'react-router-dom'

function OnGame() {
  const { currentValue, list } = useGlobalContext()
  const [housePick, setHousePick] = useState(list[0])
  let { score, setScore } = useGlobalContext()
  const [stateGame, setStateGame] = useState()
  const [stateOponent, setStateOponentGame] = useState()
  const [isSelecting, setIsSelecting] = useState(true)
  function getRandomInt() {
    return Math.floor(Math.random() * (5 - 1))
  }
  function HousePickUp() {
    return new Promise((resolve, reject) => {
      let rsolt
      let interval = setInterval(() => {
        rsolt = list[getRandomInt()]
        setHousePick(rsolt)
      }, 75)
      setTimeout(() => {
        setIsSelecting(false)
        clearInterval(interval)
        resolve(rsolt)
      }, 2000)
    })
  }
  const getResults = (currentValue, housePick) => {
    if (currentValue.value === housePick.value) {
      return 'draw'
    } else {
      if (currentValue.value === 'sissors') {
        if (housePick.value === 'paper' || housePick.value === 'lizard') {
          return 'win'
        } else {
          return 'lose'
        }
      }
      if (currentValue.value === 'paper') {
        if (housePick.value === 'rock' || housePick.value === 'spock') {
          return 'win'
        } else {
          return 'lose'
        }
      }
      if (currentValue.value === 'rock') {
        if (housePick.value === 'lizard' || housePick.value === 'sissors') {
          return 'win'
        } else {
          return 'lose'
        }
      }
      if (currentValue.value === 'lizard') {
        if (housePick.value === 'spock' || housePick.value === 'paper') {
          return 'win'
        } else {
          return 'lose'
        }
      }
      if (currentValue.value === 'spock') {
        if (housePick.value === 'sissors' || housePick.value === 'rock') {
          return 'win'
        } else {
          return 'lose'
        }
      }
    }
  }
  async function onFetch(name) {
    const house = await HousePickUp()
    let result = getResults(currentValue, house)
    setStateGame(result)
    if (result === 'draw') {
      setStateOponentGame('draw')
    }
    if (result === 'win') {
      setStateOponentGame('lose')
    }
    if (result === 'lose') {
      setStateOponentGame('win')
    }
    if (score >= 0) {
      if (result === 'win') {
        setScore((score += 1))
      }
      if (result === 'draw') {
        setScore(score)
      }
      if (result === 'lose' && score !== 0) {
        setScore((score -= 1))
      }
    }
  }

  useEffect(() => {
    onFetch()
    // eslint-disable-next-line
  }, [])

  return (
    <div className='ongame container'>
      <div className={`you-picked ${isSelecting ? '' : 'end-game'}`}>
        <h2>YOU PICKED</h2>
        <div
          className='circle-you-picked'
          style={{ background: currentValue.colorbg }}
        >
          <div className='circle-int'>
            <div
              className={`brightness ${
                isSelecting ? '' : 'end-game'
              } ${stateGame}`}
            >
              <span></span>
            </div>
            <img
              src={currentValue.imgSrc}
              className={stateGame}
              alt={currentValue.value}
            ></img>
          </div>
        </div>
      </div>
      <div
        className='results'
        style={{
          opacity: `${isSelecting ? '0' : '1'}`,
          transform: ` translateY(${isSelecting ? '50px' : '0px'})`,
        }}
      >
        <h1>YOU {stateGame}</h1>
        <Link to='/rock-paper-scissors-master' className='btn-play'>
          PLAY AGAIN
        </Link>
      </div>
      <div className={`home-picked ${isSelecting ? '' : 'end-game'}`}>
        <h2> THE HOUSE PICKED</h2>
        <div
          className='circle-you-picked'
          style={{
            background: `${housePick !== undefined ? housePick.colorbg : ''}`,
          }}
        >
          <div className='circle-int'>
            <div
              className={`brightness ${
                isSelecting ? '' : 'end-game'
              } ${stateOponent}`}
            >
              <span></span>
            </div>
            <img
              src={housePick.imgSrc}
              className={stateOponent}
              alt={housePick.value}
            ></img>
          </div>
        </div>
      </div>
    </div>
  )
}

export default OnGame
