import React, { useState, useContext, useEffect } from "react";
import Scissors from './images/icon-scissors.svg';
import Paper from './images/icon-paper.svg';
import Spock from './images/icon-spock.svg';
import Lizard from './images/icon-lizard.svg';
import Rock from './images/icon-rock.svg';

const AppContext = React.createContext();

const AppProvider = ({ children }) => {
 const list=[
   {id:1,value:'sissors',imgSrc:Scissors,colorbg:'var(--clr-scissors-gradient)'},
   {id:2,value:'paper',imgSrc:Paper,colorbg:'var(--clr-paper-gradient)'},
   {id:3,value:'rock',imgSrc:Rock,colorbg:'var(--clr-rock-gradient)'},
   {id:4,value:'lizard',imgSrc:Lizard,colorbg:'var(--clr-lizard-gradient)'},
   {id:5,value:'spock',imgSrc:Spock,colorbg:'var(--clr-cyan-gradient)'}
 ]
 const [currentValue,setCurrentValue]=useState(list[0])
 var [score,setScore]=useState(0) 
 const [isModalOpen, setIsModalOpen] = useState(false);
 const openModal = () => {
  setIsModalOpen(true);
};
const closeModal = () => {
  setIsModalOpen(false);
};
 
  useEffect(() => {
  }, [currentValue]);

  
  return (
    <AppContext.Provider
      value={{
        isModalOpen,
        score,
        list,
        currentValue,
        setCurrentValue,
        setScore,
        openModal,
        closeModal,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export const useGlobalContext = () => {
  return useContext(AppContext);
};

export { AppContext, AppProvider };
