import React from 'react'
import { Link} from 'react-router-dom';
import {useGlobalContext} from './context'
function Body() {
    const{setCurrentValue,list}=useGlobalContext()
    const handleValue=(value)=>{
        setCurrentValue(value)
    }
    return (
    <div className="game-body container">
        <div className='sticky1'></div>
        <div className='sticky2'></div>
        <div className='sticky3'></div>
        <div className='sticky4'></div>
        <div className='sticky5'></div>
        {list.map((item,index)=>{

            return(
                <div className={`circle${index+1}`} key={item.id} style={{background:item.colorbg}}>
                    <Link to="/rock-paper-scissors-master/onGame" onClick={()=>handleValue(item)}><img src={item.imgSrc} alt={item.value}></img></Link>
                </div>
            )
        })}
   </div>
    )
}

export default Body
